import React, {Component} from 'react';

const styles = {
  style: {
  display: 'inline-block',
  margin: '16px 32px 16px 0',
  listStyleType: 'none',
  width: '75%',
  color: 'rgba(0,0,0,0.87)',

},
 listItem: {
  
  height: '32px',
  padding: '11px 0px 0px 16px'
 },


 selected: {
    height: '32px',
    color: 'rgba(0,0,0,0.87)',
    background: 'rgba(0,0,0,0.07)',
     padding: '11px 0px 0px 16px'
  }

};

class MyMenu extends Component{
  
  render(){
  
    return(

      <div>
      <ul style={styles.style}>
        <li style={styles.listItem}>General</li>
        <li style={styles.selected}>Notification</li>
        <li style={styles.listItem}>Manage Apps</li>
       </ul>
                          
  </div>
    )
  }
}
 

export default MyMenu;


