import React, { Component } from 'react';
import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider';
import AppBarExample from './AppBarExample';
import MyMenu from './MyMenu';
import injectTapEventPlugin from 'react-tap-event-plugin';
import Toggle from 'material-ui/Toggle';
import {RadioButton, RadioButtonGroup} from 'material-ui/RadioButton';
import Checkbox from 'material-ui/Checkbox';
import driveLogo from './logo_drive.png';
import slackLogo from './slack_logo.png';
 


import './App.css';
// Needed for onTouchTap 
// http://stackoverflow.com/a/34015469/988941 
injectTapEventPlugin();

const styles = {
 
  radioButton: {
    marginBottom: 16,
  },

  checkbox: {
    marginBottom: 16,
    fontSize: 13,
  },

   slack: {
    marginTop: 32,
  },
};

class App extends Component {
  state = {
    notificationOn: true,
    emailOn: true,
    channelSelected: 'drive',

  }
  
  toggleNotification=() => {
    this.setState({notificationOn: !this.state.notificationOn})
  }
  toggleEmail=() => {
    this.setState({emailOn: !this.state.emailOn})
  }
  changeChannel=(channel) => {
    
    this.setState({channelSelected: channel})
    
  } 

  render() {
  
    const notificationOn = this.state.notificationOn;
    const emailOn = this.state.emailOn;
    const channelSelected = this.state.channelSelected;
    return (
     
    <MuiThemeProvider>
    <div>
      <AppBarExample />
      
      <div  className='leftMenu'>
        <MyMenu />
      </div>

      <div className='canvas'>
        <div className='notifications section'>
           <Toggle
            toggled={notificationOn}
            label="Notifications"
            onToggle={this.toggleNotification}
          />
          {!notificationOn? <p className="des" id="n-des">You won't receive any notifications</p>:
          <div>
            {channelSelected=='drive'? <p className="des" id="n-des">You will receive notifications from Google Drive</p>:
            <p className="des" id="n-des">You will receive notifications from Slack</p>}
            <div className='notificationSettings'>
            <RadioButtonGroup valueSelected={channelSelected} onChange={(event, value)=>this.changeChannel(value)}>
               <RadioButton
                  value="drive"
                  
                  label={ <div><img className='drivelogo' width='24px' src={driveLogo}/> <span className='productName'>Google Drive</span></div>}
                  style={styles.radioButton}
                   />
                     </RadioButtonGroup>
             {channelSelected=='drive'? <div className='deviceSettings'>
                  <p className='subtitle' >Manage Chrome Desktop notifications</p>
                  
                  <Checkbox
                  label="Shared items"
                  defaultChecked='true'
                  style={styles.checkbox}
                  />

                   <Checkbox
                  label="Requests for access"
                  defaultChecked='true'
                  style={styles.checkbox}
                  />

                  <Checkbox
                  label="Comments and suggestions"
                  defaultChecked='true'
                  style={styles.checkbox}
                  />
                  
                </div> :
                <div></div>}
 
            <RadioButtonGroup style={styles.slack} valueSelected={channelSelected} onChange={(event, value)=> this.changeChannel(value)}>
               <RadioButton
                  value="slack"
                  label={ <div><img height='24px' src={slackLogo}/>  </div>}
                  style={styles.radioButton}
                 
                  />
            </RadioButtonGroup>   

          </div> 
          </div>}
          
        
        </div>

        <div className='emails section'>
           <Toggle
            toggled={emailOn}
            label="Emails"
            onToggle={this.toggleEmail}
          />

          {emailOn? <p className="des" >You'll receive emails when someone comments or makes a suggestion in a file, shares or requests access to a file, folder or Team Drive</p>:
          <p className="des">You'll only receive account related messages via email</p>}
        </div>
      </div>
      
      
      
      </div>
      </MuiThemeProvider>
     
    );
  }
}

export default App;



