import React from 'react';
import AppBar from 'material-ui/AppBar';
import IconButton from 'material-ui/IconButton';
import NavigationArrowBack from 'material-ui/svg-icons/navigation/arrow-back';
import FlatButton from 'material-ui/FlatButton';
import FontIcon from 'material-ui/FontIcon';
import {blue500, red500, greenA200} from 'material-ui/styles/colors';

/**
 * A simple example of `AppBar` with an icon on the right.
 * By default, the left icon is a navigation-menu.
 */
const styles = {
  title: {
    cursor: 'pointer',
    color:'rgba(0,0,0,0.87)'
  },

  iconStyle: {
  marginRight: 24,
  fill:'rgba(0,0,0,0.87)',
  color:'rgba(0,0,0,0.87)'
},
};


const AppBarExample = () => (
   <AppBar
    title={<span style={styles.title}>Settings</span>}
    style={{backgroundColor: 'white'}}
    iconElementLeft={<IconButton iconStyle={styles.iconStyle}><NavigationArrowBack  /></IconButton>}
    iconElementRight={<IconButton iconStyle={styles.iconStyle}> <FontIcon
      className="muidocs-icon-action-home" color="blue500"
      style={styles.iconStyle}
    /></IconButton>}
  />
);

export default AppBarExample;


